<template>
  <div id="style-operate">
    <el-dialog v-model="dialogFormVisible" :before-close="beforeClose" :title="titleName" width="1200" draggable>

      <el-form :inline="true" :model="fromData" class="demo-form-inline" :label-width="100" label-position="right">
        <el-form-item label="编码/名称: ">
          <el-input v-model="fromData.spuName" placeholder="请输入编码/名称"></el-input>
        </el-form-item>
        <el-form-item label="系列: ">
          <el-select filterable v-model="fromData.series" placeholder="请选择系列...">
            <el-option v-for="item in selectData.seriesList" :key="item.id" :label="item.name" :value="item.id"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="功能: ">
          <el-select filterable v-model="fromData.function" placeholder="请选择功能...">
            <el-option v-for="item in selectData.functionList" :key="item.id" :label="item.name" :value="item.id"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="创建人: ">
          <el-select filterable v-model="fromData.createUser" placeholder="请选择创建人...">
            <el-option v-for="item in selectData.userList" :key="item.id" :label="item.name" :value="item.id"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="listData()" icon="Search"> {{ $t('common.button.search') }} </el-button>
          <el-button type="info" @click="reset()" icon="Loading">  {{ $t('common.button.reset') }} </el-button>
        </el-form-item>
      </el-form>

      <el-table :data="tableData" ref="multipleTable" tooltip-effect="dark" stripe border fit highlight-current-row :empty-text="$t('common.table.noData')" style="width: 100%; margin-bottom: 5px;" max-height="300">
        <el-table-column type="selection" width="55" show-overflow-tooltip></el-table-column>
        <el-table-column fixed type="index" :label="$t('common.table.number')" width="90"/>
        <el-table-column prop="spuCode" label="产品编码" width="180" show-overflow-tooltip></el-table-column>
        <el-table-column prop="spuName" label="产品名称" width="150" show-overflow-tooltip></el-table-column>
        <el-table-column prop="spuImg" label="产品图片" width="100">
          <template v-slot="{ row }"><el-avatar shape="square" :size="40" :src="getUrl(row.spuImg)" /></template>
        </el-table-column>
        <el-table-column prop="seriesName" label="系列名称" width="150" show-overflow-tooltip></el-table-column>
        <el-table-column prop="functionName" label="功能名称" width="120" show-overflow-tooltip></el-table-column>
        <el-table-column prop="specs" label="规格" width="180" show-overflow-tooltip></el-table-column>
        <el-table-column prop="color" label="颜色" width="180" show-overflow-tooltip></el-table-column>
        <el-table-column prop="shape" label="形状" width="210" show-overflow-tooltip></el-table-column>
        <el-table-column prop="material" label="材质" width="180" show-overflow-tooltip></el-table-column>
        <el-table-column prop="model" label="型号" width="180" show-overflow-tooltip></el-table-column>
        <el-table-column prop="remarks" label="备注" width="180" show-overflow-tooltip></el-table-column>
        <el-table-column prop="createUserNmae" label="创建人" width="180" show-overflow-tooltip></el-table-column>
        <el-table-column prop="createTime" label="创建时间" width="180" show-overflow-tooltip></el-table-column>
        <el-table-column prop="enable" label="启用状态" width="180" show-overflow-tooltip></el-table-column>
      </el-table>

      <div class="demo-pagination-block">
        <el-pagination background v-model:current-page="pageNum" v-model:page-size="pageSize" :page-sizes="[10, 30, 50, 75, 100]"
            :small="false" layout="prev, pager, next, sizes, jumper, total" :total="total" @size-change="handleSizeChange" @current-change="handleCurrentChange"/>
      </div>

      <template #footer>
      <span class="dialog-footer">
        <el-button @click="closeButton()">
          {{ $t('common.button.cancel') }}
        </el-button>
        <el-button type="primary" @click="submitButton()"> {{ $t('common.button.confirm') }} </el-button>
      </span>
      </template>
    </el-dialog>
  </div>
</template>

<script>
import {getFunctionPullDown, getSeriesPullDown, getUserAdminPullDown} from "@/http/PullDownApi";
import {listSpuInfo} from "@/http/spuData/spuApi";
import {resourceIsUser} from "@/util/resource";

export default {
  name: "style-operate",
  // data: 对象就是要渲染到页面上的数据
  data: function () {
    return {
      titleName: '产品选入框',
      // 控制对话框大开闭
      dialogFormVisible: false,

      // 查询框数据集合对象
      selectData: {},
      // 查询条件对象
      fromData: {},

      // 列表数据
      tableData: [],
      total: 0,           // 总条数
      pages: 0,           // 总页数
      pageNum: 1,         //当前页码
      pageSize: 30,        // 每页条数

      setDataList: [],  // 传递已选中的数据 用于回显
      updateList: [],   // 翻页时储存选中的数据
    }
  },

  // moubted: 页面加载完毕后执行
  mounted() {

  },

  // methods: 对象中的方法，可以在页面中调用
  methods: {
    // 获取按钮权限
    isButton(symbolStr) {
      return resourceIsUser(symbolStr);
    },

    // 父组件调用负责子组件的初始化: 查询条件集合 数据列表 回显
    getPullDownData() {
      // 获取查询条件的下拉数据
      getUserAdminPullDown().then(res => {
        if (res.data.status == 200) this.selectData.userList = res.data.data;
      }).catch((erro) => { });
      getSeriesPullDown().then(res => {
        if (res.data.status == 200) this.selectData.seriesList = res.data.data;
      }).catch((erro) => { });
      getFunctionPullDown().then(res => {
        if (res.data.status == 200) this.selectData.functionList = res.data.data;
      }).catch((erro) => { });
      // 查询选入框列表数据
      this.listData();
    },

    // 重置按钮
    reset() {
      this.fromData = {};
      this.listData();
    },
    // 列表数据请求
    listData() {
      this.fromData.pageSize = this.pageSize;
      this.fromData.pageNum = this.pageNum;
      listSpuInfo(this.fromData).then(res => {
        this.tableData = res.data.data.list;
        // 给分页组件的数据赋值
        this.total = res.data.data.total;
        this.pages = res.data.data.pages;
        this.pageNum = res.data.data.pageNum;
        this.pageSize = res.data.data.pageSize;

        this.$nextTick(() => {
          // 在下一次 DOM 更新周期中获取到新的 optionData
          this.setSelection();
        }, 300);
      }).catch((erro) => {
        console.log(erro, 'erro')
      });
    },
    // 设置每页条数方法 val=条数
    handleSizeChange(val) {
      // 翻页时储存选中的数据
      this.updateList = [...this.updateList, ...this.$refs.multipleTable.getSelectionRows()];
      console.log(`每页 ${val} 条`);
      this.pageNum = 1;                       // 修改当前页为第1页
      this.pageSize = val;               // 修改每页条数
      this.listData();
    },
    // 点击 前一页、后一页、页码 事件
    handleCurrentChange(val) {
      // 翻页时储存选中的数据
      this.updateList = [...this.updateList, ...this.$refs.multipleTable.getSelectionRows()];
      console.log(`当前页: ${val}`);
      this.pageNum = val;                 // 修改当前页码
      this.listData();
    },

    // 指定图片的前缀
    getUrl(photoUrl) {
      return process.env.VUE_APP_BACKEND_API_URL + photoUrl;
    },
    // 回显选择数据 注意异步问题
    setSelection() {
      if (!this.setDataList) {
        return;
      }
      let ids = [];
      this.setDataList.forEach((item) => {
        ids.push(item.spuId);
      });
      this.tableData.forEach((item) => {
        if (ids.includes(item.id)) {
          this.$refs.multipleTable.toggleRowSelection(item);
        }
      });
    },

    // 点击遮罩和x时执行
    beforeClose(done) {
      this.closeButton();
      done(); // 必须调用 done，以继续关闭对话框
    },
    // 取消按钮
    closeButton() {
      this.dialogFormVisible = false;
      this.isReadonly = false;
      this.form = {};
      this.rowData = {};
    },
    // 确认按钮 关闭页面传递选择的数据到父组件
    submitButton() {
      let newData = this.$refs.multipleTable.getSelectionRows();
      newData.forEach((item) => { item.spuId = item.id; });
      // 调整选中的数据
      if (this.setDataList) {
        // 深拷贝传入的数据 防止出错
        const getData = JSON.parse(JSON.stringify(this.setDataList))
        // new Map 储存数据
        const map = new Map();
        // 将翻页或者调整数据的时候备份的选中的数据追加
        newData = [...newData, ...this.updateList];
        // id覆盖 map添加时去重
        newData.forEach((item) => { item.spuId = item.id; map.set(item.spuId, item); });
        // 将之前的传入的数据进行覆盖 保留之前的原有数据
        getData.forEach((item) => {
          if (map.has(item.spuId)){
            map.set(item.spuId, item)
          }
        });
        newData = Array.from(map.values());
      }
      // 关闭选入框
      this.dialogFormVisible = false;
      // 获取选中的数据发送到父组件
      this.$emit('childClick', newData);
    },
  },
}
</script>

<!--组件css样式 scoped:表示该样式只在此组件生效-->
<style scoped>
.el-button--text {
  margin-right: 15px;
}

.el-select {
  width: 300px;
}

.el-input {
  width: 300px;
}

.dialog-footer button:first-child {
  margin-right: 10px;
}

.avatar-uploader .avatar {
  width: 178px;
  height: 178px;
  display: block;
}
</style>

<style>
.avatar-uploader .el-upload {
  border: 1px dashed var(--el-border-color);
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
  transition: var(--el-transition-duration-fast);
}

.avatar-uploader .el-upload:hover {
  border-color: var(--el-color-primary);
}

.el-icon.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 178px;
  height: 178px;
  text-align: center;
}
</style>
